<template>
  <el-card id="add-blog">
    <el-form :model="blog" :rules="rules" ref="blog" label-width="100px" class="demo-blog">
    <el-form-item label="" prop="title">
        <el-input v-model="blog.title" style="width: 300px"></el-input>
    </el-form-item>
    <el-form-item label="" prop="author">
        <el-input v-model="blog.author" style="width: 200px"></el-input>
    </el-form-item>
    <el-form-item label="" prop="delivery">
        <el-switch v-model="blog.delivery"></el-switch>
    </el-form-item>
    <el-form-item label="" prop="type">
        <el-checkbox-group v-model="blog.type">
        <el-checkbox label="" title="type"></el-checkbox>
        <el-checkbox label="" title="type"></el-checkbox>
        <el-checkbox label="" title="type"></el-checkbox>
        <el-checkbox label="" title="type"></el-checkbox>
        </el-checkbox-group>
    </el-form-item>
    <el-form-item label="" prop="content">
        <el-input type="textarea" v-model="blog.content" style="width: 80%"></el-input>
    </el-form-item>
    
    <el-form-item>
        <el-button type="primary" @click="submitForm('blog')"></el-button>
        <el-button @click="resetForm('blog')"></el-button>
    </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
  export default {
      
    data() {
      return {
        blog: {
          title: '',
          author: '',
          delivery: false,
          type: [],
          content: ''
        },
        rules: {
          title: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
          ],
          author: [
              {required: true, message: '', trigger: 'blur'},
          ],
          type: [
            { type: 'array', required: true, message: '', trigger: 'change' }
          ],
          content: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formtitle) {
        this.$refs[formtitle].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formtitle) {
        this.$refs[formtitle].resetFields();
      }
    }
  }
</script>

<style>
#add-blog {
    margin: 50px 20%;
}
.el-form-item {
    margin-top: 50px;
}
</style>



